  blockquote.wp-block-quote p:first-of-type {
    padding-top: 0; }
  blockquote.wp-block-quote cite {
    font-family: "Lato", sans-serif;
    color: #5A8C2C;
    padding-top: 0;
    padding-bottom: 0; }
